import { Component, OnInit } from '@angular/core';
import { BitCoinService } from '../services/bit-coin.service'

@Component({
  selector: 'app-statistics',
  templateUrl: './statistics.component.html',
  styleUrls: ['./statistics.component.scss']
})
export class StatisticsComponent implements OnInit {
  marketPriceData = null
  transData = null

  constructor(private bitCoinService: BitCoinService) { }

  ngOnInit() {
    this.bitCoinService.getMarketPrice().subscribe((marketPrice: any) => {
      this.marketPriceData = marketPrice
    })
    this.bitCoinService.getConfirmedTransactions().subscribe((transData)=> {
      this.transData = transData
    })
  }
}