import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'

@Injectable({
  providedIn: 'root'
})
export class BitCoinService {

  constructor(private http: HttpClient) { }

  getMarketPrice() {
    const priceChart =  this.http.get('https://api.blockchain.info/charts/market-price?timespan=1months&format=json&cors=true')
    return priceChart
  }

  getConfirmedTransactions() {
    const transChart =  this.http.get('https://api.blockchain.info/charts/median-confirmation-time?timespan=1months&format=json&cors=true')
    console.log(transChart);
    
    return transChart
  }
  getRate() {
    const rate =  this.http.get(`https://blockchain.info/tobtc?currency=USD&value=1&cors=true`);
    return rate
  }
}
