import { Component, OnInit, Input } from '@angular/core';
import { ChartDataSets, ChartOptions } from 'chart.js';
import { Color, Label } from 'ng2-charts';


@Component({
  selector: 'app-market-price-chart',
  templateUrl: './market-price-chart.component.html',
  styleUrls: ['./market-price-chart.component.scss']
})
export class MarketPriceChartComponent implements OnInit {
  @Input() marketPriceData

  lineChartData: ChartDataSets[] = [
    { data: [], label: 'Market Price (USD)' },
  ];

  lineChartLabels: Label[] = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];

  public lineChartOptions: (ChartOptions & { annotation: any }) = {
    responsiveAnimationDuration: 5000,
    annotation: true,
    responsive: true
  };
  lineChartColors: Color[] = [
    {
      borderColor: 'lightBlue',
      backgroundColor: 'rgba(95,195,255,1)',
    },
  ];

  lineChartLegend = true;
  lineChartType = 'line';
  lineChartPlugins = [];

  constructor() { }

  ngOnInit() {
    
    this.marketPriceData.values.map((value) => {
      this.lineChartData[0].data.push(value)
    })
  }

}
