import { Component, OnInit, Input } from '@angular/core';
import { ChartDataSets, ChartOptions } from 'chart.js';
import { Color, Label } from 'ng2-charts';

@Component({
  selector: 'app-trans-chart',
  templateUrl: './trans-chart.component.html',
  styleUrls: ['./trans-chart.component.scss']
})
export class TransChartComponent implements OnInit {
  @Input() transData

  lineChartData: ChartDataSets[] = [
    { data: [], label: 'Median Confirmation Time' },
  ];

  lineChartLabels: Label[] = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];

  public lineChartOptions: (ChartOptions & { annotation: any }) = {
    responsiveAnimationDuration: 5000,
    annotation: true,
    responsive: true
  };
  lineChartColors: Color[] = [
    {
      borderColor: 'lightBlue',
      backgroundColor: 'rgba(212,50,70,0.88)',
    },
  ];
  
  lineChartLegend = true;
  lineChartType = 'line';
  lineChartPlugins = [];

  constructor() { }

  ngOnInit() {
    console.log(this.transData);
    
    this.transData.values.map((value) => {
      this.lineChartData[0].data.push(value)
    })
  }
}
